html,
body {
  margin: 0;
  padding: 0;
  font-size: 14px;
}

body {
  min-height: 100%;
  font-family: Roboto;
  background: #f1f1f1;
}

a {
  color: #333333;
}

article hr {
  margin: 10px 14px;
  float: none;
  width: initial;
}

.is-logo {
  float: left;
  margin-left: 18px;
}

.logo {
  margin-left: 15px;
  font-size: 30px;
  font-weight: bold;
  float: left;
}

.logo:hover {
  text-decoration: none;
}

.logo i {
  margin-left: 5px;
  color: #e91d00;
}

header {
  height: 70px !important;
  background: #ffffff;
  padding: 10px;
  border-bottom: 1px solid #e8e8e8;
  position: relative;
  left: 0;
  right: 0;
  z-index: 2;
}

.searchbox-container {
  margin-left: 20px;
  width: 1000px;
}

.searchbox-container .input-group {
  margin-top: 5px;
}

.searchbox-container .form-control:focus {
  outline: none;
  box-shadow: none;
}

.searchbox-container button {
  position: absolute;
  color:#5468FF;
  border-radius: 0;
  z-index: 1000;
}

section {
  background: #f1f1f1;
  min-height: 100%;
  z-index: 1;
}

section aside {
  position: absolute;
  top: 67px;
  left: 0;
  bottom: 0;
  width: 230px;
  background: #ffffff;
  border-right: 1px solid #e8e8e8;
}

.nav {
  margin: 0 20px;
}

.nav li a {
  display: block;
  padding: 2px 10px;
  margin: 10px 0;
}

.nav li a:hover {
  color: #ffffff;
  background: #5468FF;
}

.nav li.separator {
  height: 1px;
  background: #e8e8e8;
}

section aside h5 {
  color: #ce1312;
  margin-left: 30px;
  text-transform: uppercase;
  font-size: 10px;
  margin-top: 20px;
}

section aside .badge {
  font-size: 0.8em;
  background: #bbbbbb;
  position: relative;
  top: 1px;
}

#genres .item {
  display: block;
  cursor: pointer;
  padding: 4px 4px 4px 8px;
  font-weight: normal;
  font-size: 0.9em;
  margin: 0 0 -1px;
}

#genres .item:hover {
  background: #5468FF;
  color: #ffffff;
  text-decoration: none;
}

#genres .active .item {
  border: 1px solid #ce1312;
  margin-top: -1px;
}

#genres .active .badge {
  background: #5468FF;
}

section article {
  margin-top: 7px;
  margin-bottom: 10px;
  margin-left: 237px;
  margin-right: 10px;
  padding: 10px 0;
  max-width: 100%;
  background: #ffffff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

.ais-Stats-text {
  padding-right: 14px;
  font-size: 0.8em;
  line-height: 24px;
}

#hits {
  padding: 0px 15px;
}

.ais-Pagination {
  box-shadow: none;
  border: none;
  border-radius: initial;
  padding: 0px;
}

.ais-Pagination-item {
  display: inline-block;
  padding: 0px;
}

.ais-Pagination-item + .ais-Pagination-item {
  margin-left: 4px;
}

.ais-Pagination-item--disabled {
  display: none;
}

.ais-Pagination-link {
  display: block;
  background: #f8f8f8;
  padding: 3px 8px;
  color: #333333;
}

.ais-Pagination-item--selected .ais-Pagination-link {
  border-color: #b5b5b5;
  background: #e8e8e8;
}

.ais-Pagination-link:hover {
  text-decoration: none;
  border-color: #cfcfcf;
  background: #ebebeb;
  color: #333333;
}

.ais-Hits-list {
  margin: 0;
}

.ais-Hits-item {
  margin: 0;
  padding: 0;
  border: none;
  box-shadow: none;
  width: 100%;
}

.hit {
  margin-bottom: 10px;
  border: 1px solid #f3f3f3;
  padding: 10px;
}

.hit em {
  font-style: normal;
  background: #ffffd4;
  text-decoration: underline;
}

.hit .media-object {
  height: 130px;
  width: 130px;
  overflow: hidden;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
}

.hit .media-heading {
  color: #167ac6;
  font-weight: normal;
  font-size: 18px;
}

.thank-you {
  font-size: 0.8em;
  margin-top: 18px;
  margin-left: 30px;
}

.tab {
  display: inline-block;
  margin-left: 40px;
}

.thank-you a {
  color: #ce1312;
}

.stars {
  margin-left: 5px;
}

.star {
  /* item star */
  display: inline-block;
  width: 1em;
  height: 1em;
}

.star:before {
  content: '\2605';
  color: #fbae00;
}

.star__empty {
  /* empty star */
  display: inline-block;
  width: 1em;
  height: 1em;
}

.star__empty:before {
  content: '\2606';
  color: #fbae00;
}

.genre,
.year {
  margin: 12px 0;
}

.genre .badge + .badge {
  margin-left: 4px;
}

.year {
  font-weight: bold;
}

.genre .badge {
  background: #bbbbbb;
}

.ais-RatingMenu {
  margin: 0 24px 0 28px;
}

.textResult {
    padding-top: 5px;
}
.primaryAttributeStyles {
    display:block;
    margin-bottom: .25rem;
    font-weight: 600;
    color: #3c4fe0;
    text-decoration: inherit;
    background-color: transparent;
    cursor: pointer;
  }

.secondAttributeStyles {
    margin-bottom: .25rem;
    font-weight: 400;
    color: #23263b;
    text-decoration: inherit;
    margin: 0px 0px 4px;
    white-space: pre-line;
  }
  .dateStyles {
    font-weight: 400;
    color: #9E9E9E;
    text-decoration: inherit;
    margin: 0px 0px 4px;
  }

  @media  screen and (max-width: 600px) {
    section aside {
        display: none;
    }
    .is-logo {
        margin-left: 0px;
        padding-right: 5px;
    }

    section article {
        margin-left: 0px;
        margin-right: 0px;
    }

    .ais-Stats-text {
        padding-right: 50px;
    }

    .searchbox-container {
       width: 400px;
    }
  }

  @media screen and (max-width: 400px) {
    .searchbox-container {
       width: 300px;
       margin-left: 0px
    }
  }

  @media  screen and (max-width: 800px) {
    .searchbox-container {
       width: 500px;
    }
  }