.card__heading{
    font-weight: 600;
    font-size: 30px;
    margin-bottom: 10px;
  }

  .card__Pricing-container>small{
    font-weight: 500;
    color: gray;
  }

  .price-tag{
    font-weight: 600;
    font-size: 25px;
  }
  .Price__button-container{
    display: flex;
    justify-content: center;
  }
  .Price__button-container>button{
    display: block;
      font-weight: 300;
      font-size: 14px;
      border-radius: 20px;
      padding: 0px 20px;
      background-color: #00077B;
      text-transform: uppercase;
      color: #fff;
      letter-spacing: 0.25px;
      -webkit-transition: all 0.3s ease 0s;
      -moz-transition: all 0.3s ease 0s;
      -o-transition: all 0.3s ease 0s;
      transition: all 0.3s ease 0s;
      height: 38px;
      line-height: 38px;
      border: transparent;
      margin-top: 15px;
  }
  .card__Pricing-item{
    padding: 20px;
    display: flex;
    height: 100%;
    text-align: center ;
    background-color: #F9FAFF;
    border-radius: 5px;
    box-shadow: 0px 5px 10px #00077B33;
    transition: .4s;
    justify-content: center;
   
  }
  .card__Pricing-item.active{
    padding: 20px;
    display: flex;
    height: 100%;
    text-align: center ;
    background-color: #00077B;
    border-radius: 5px;
    box-shadow: 0px 5px 10px #00077B33;
    transition: .4s;
    justify-content: center;
  }
  .card__Pricing-item.active p{
    color: white;
  }
  .card__Pricing-item.active h2{
    color: white;
  }
  .card__Pricing-item.active small{
    color: white;
  }
  .card__Pricing-item.active button{
  background: #e96504;
  }
 
  .card__Pricing-item:hover{
    margin-top: -20%
  }
  .price__feature{
    margin-top: 20%;
    text-align: initial;
  }
  .list-item{
    position: relative;
    display: flex;
      justify-content: start;
      align-items: baseline;
      flex-wrap: nowrap;
      margin-bottom: 5px;
  }
  .list-item>figure>img{
    margin-top: 2px;
    width: 15px;
    margin-right: 10px;
  }
  .list-item p{
    display: inline-block;
    line-height: 1.5;
  }